import './Book.css';
function Book(data) {
    data=data.data
    console.log(data)
    return (

        <div className="Book">
            <div>
                <img alt="" src={data.logo}/>
            </div>
            <div >
                <p>{data.title}</p>
                <small>{data.price}</small>
            </div>
        </div>
    );
}

export default Book;